								if(callBackPage) {
									let params = objectToUrlParams(callBackPage.params)
									uni.navigateTo({
										url: callBackPage.url+params
									})
								}
								uni.removeStorageSync('callBackPage');
								that.set_isSearchDetail(3)
                            } else if (res.cancel) {
                                console.log('用户点击取消');
                            }
                        }
                    });
				}else{
					this.$KM.showToast(res.data.msg)
				}
			})
